import React, { useCallback, useState } from 'react';
import { Link, graphql, useStaticQuery } from 'gatsby';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';

import VersionSelect from './version-select';
import { tocFragment, allDirectoryNodes } from '../fragments/documentation';
import { siteFragment } from '../fragments/site';
import { getDocsVersion } from '../utils/version-docs';

export default function DocToc() {
  const data = useStaticQuery(graphql`
    query TocQuery {
      site {
        ...SiteMetadata
      }
      allDirectory(filter: { relativeDirectory: { eq: "documentation" } }) {
        ...AllDocDirectories
      }
      allMdx(
        filter: { internal: { contentFilePath: { regex: "/documentation//" } } }
        sort: [{ frontmatter: { order: ASC } }]
      ) {
        ...MdxTableOfContents
      }
    }
  `);

  const allDocs = data.allMdx.nodes;
  const versions = data.allDirectory.nodes.reverse().map((node) => node.base);

  const [selectedVersion, setSelectedVersion] = useState(versions[0]);
  const docs = getDocsVersion(allDocs, selectedVersion);
  const handleVersionChange = useCallback((event) => {
    setSelectedVersion(event.target.value);
  }, []);

  return (
    <>
      <article className="content">
        <Toolbar disableGutters sx={{ mb: 3 }} variant="secondary">
          <Box sx={{ flexGrow: 1 }}>
            <h2>Documentation</h2>
          </Box>
          <Box sx={{ maxWidth: 120 }}>
            <VersionSelect
              versions={versions}
              selectedVersion={selectedVersion}
              onChange={handleVersionChange}
            />
          </Box>
        </Toolbar>
      </article>
      {docs.map((node) => {
        const title = node.frontmatter.title;
        const toc = node.tableOfContents?.items;
        return (
          <article className="content" key={node.fields.slug}>
            <h3>
              <Link to={node.fields.slug}>{title}</Link>
            </h3>
            <ul>
              {toc &&
                toc.map((item, i) => (
                  <li key={i}>
                    <Link to={`${node.fields.slug}${item.url}`}>
                      {item.title}
                    </Link>
                  </li>
                ))}
            </ul>
          </article>
        );
      })}
    </>
  );
}
