import React from 'react';
import { Link, graphql, useStaticQuery } from 'gatsby';

export default function Toc({ directory }) {
  const data = useStaticQuery(graphql`
    query Query {
      allDirectory {
        nodes {
          base
          relativeDirectory
        }
      }
      allMdx(sort: [{ frontmatter: { order: ASC } }]) {
        nodes {
          id
          parent {
            id
            internal {
              contentFilePath
            }
          }
          tableOfContents
          frontmatter {
            title
          }
          internal {
            contentFilePath
          }
          fields {
            slug
          }
        }
      }
    }
  `);

  const nodes = directory
    ? data.allMdx.nodes
        .filter((node) => node.fields.slug.split('/')[1] === directory)
        .filter((node) => !node.internal.contentFilePath.includes('index.mdx'))
    : data.allMdx.nodes;

  return (
    <>
      {nodes.map((node) => {
        const title = node.frontmatter.title;
        const toc = node.tableOfContents?.items;
        return (
          <article className="content" key={node.fields.slug}>
            <h3>
              <Link to={node.fields.slug}>{title}</Link>
            </h3>
            <ul>
              {toc &&
                toc.map((item, i) => (
                  <li key={i}>
                    <Link to={`${node.fields.slug}${item.url}`}>
                      {item.title}
                    </Link>
                  </li>
                ))}
            </ul>
          </article>
        );
      })}
    </>
  );
}
